<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="searchPatient"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addPatient"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!-- -->
    <el-table :data="patientsList" highlight-current-row v-loading="listLoading" @selection-change="selsChange"
              style="width: 100%;" height="500"><!--height-->
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="40" fixed>
      </el-table-column>
      <el-table-column prop="name" label="" width="90" sortable fixed>
      </el-table-column>
      <el-table-column prop="birth" label="" width="100" sortable>
      </el-table-column>
      <!-- <el-table-column prop="age" label="" width="100" sortable>
      </el-table-column> -->
      <el-table-column prop="phone" label="" width="110" sortable>
      </el-table-column>
      <el-table-column prop="hospital" label="" width="150" sortable>
      </el-table-column>
      <el-table-column prop="career" label="" min-width="90" sortable>
      </el-table-column>
      <el-table-column label="" width="610">
        <template v-slot="scope">
          <el-button-group>
          <el-button type="primary" size="small" @click="openInfoForm(scope.$index, scope.row)"></el-button>
          <el-button type="success" size="small" @click="openSummaryForm(scope.$index, scope.row)"></el-button>
          <el-button type="info"    size="small" @click="openHistoryForm(scope.$index, scope.row)"></el-button>
          <el-button type="warning" size="small" @click="openExperimentForm(scope.$index, scope.row)"></el-button>
          <el-button type="primary" size="small" @click="openBxrayForm(scope.$index, scope.row)">B</el-button>
          <el-button type="success" size="small" @click="openCureForm(scope.$index, scope.row)"></el-button>
          <!-- <el-button size="small" style="background:purple"></el-button> -->
          </el-button-group>
          <el-button type="danger" size="small" style="margin-left:8px" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
      <span style="margin-left:100px">{{totalNum}}</span>
      <el-pagination layout="prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="10" :total="totalNum"
                    style="float:right;">
      </el-pagination>
    </el-col>

    <!-- dialog -->
    <InfoForm ref="infoForm"></InfoForm>
    <!-- dialog -->
    <SummaryForm ref="summaryForm"></SummaryForm>
    <!-- dialog -->
    <HistoryForm ref="historyForm"></HistoryForm>
    <!-- dialog -->
    <ExperimentForm ref="experimentForm"></ExperimentForm>
    <!-- Bdialog -->
    <BxrayForm ref="bxrayForm"></BxrayForm>
    <!-- dialog -->
    <CureForm ref="cureForm"></CureForm>

    <!-- dialog -->
    <AddPatient ref="addPatient" ></AddPatient>

  </section>
</template>

<script>
import util from '@/common/js/util'
// axios,express
import {apiGetPatientsList, apiRemovePatient, apiSearchPatient, batchRemoveUser} from '@/api/api'
// api
import {apiGetPatientInfoForm,apiGetPatientSummaryForm,apiGetPatientHistoryForm,
        apiGetPatientExperimentForm,apiGetPatientBxrayForm,apiGetPatientCureForm } from '@/api/api'
// 
import {AddPatient,InfoForm,SummaryForm,HistoryForm,ExperimentForm,BxrayForm,CureForm} from '@/components/forms'
export default {
  name:'Table',
  components:{AddPatient,InfoForm,SummaryForm,HistoryForm,ExperimentForm,BxrayForm,CureForm},
  data () {
    return {
      filters: {
        name: ''
      },
      patientsList: [], // 
      totalNum: 0, //  
      page: 1, //
      listLoading: false,
      sels: [], // 

    }
  },
  methods: {
    // 
    searchPatient () {
      let para = {
        name:'',
        page:1
        //phone:'', hospital:'', birth:'', career:'', address:''
      }
      apiSearchPatient(para).then( (res) => {
        this.patientsList = res.data.searchResults
        this.totalNum = res.data.searchResultsNum
        this.listLoading = false
      })
    },
    // dialog
    addPatient ( ) {
      this.$refs.addPatient.$emit("addEvent")
    },
    handleCurrentChange (val) {
      this.page = val
      this.getPatients()
    },
    // 
    getPatients () {
      let para = {
        page: this.page
      }
      this.listLoading = true
      apiGetPatientsList(para).then((res) => {
        console.log(res.data)
        this.patientsList = res.data.patientsList
        this.totalNum = res.data.totalNum
        this.listLoading = false
      })
    },
    // 
    handleDel: function (index, row) {
      this.$confirm('?', '', {type: 'warning'})
      .then(() => {
        this.listLoading = true
        let para = { url: row.url }
        console.log(row.url)
        apiRemovePatient(para).then((res) => {
          this.listLoading = false
          this.$message({
            message: '',
            type: 'success'
          })
          this.getPatients()
        })
      }).catch(() => {})
    },

    selsChange: function (sels) {
      this.sels = sels
    },
    // 
    batchRemove: function () {
      var ids = this.sels.map(item => item.id).toString()
      this.$confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        let para = { ids: ids }
        batchRemoveUser(para).then((res) => {
          this.listLoading = false
          this.$message({
            message: '',
            type: 'success'
          })
          this.getPatients()
        })
      }).catch(() => {

      })
    },
    //
    openInfoForm (index, row) {
      let para = {page: this.page, url: row.url}
      apiGetPatientInfoForm(para)
      .then((res)=> {
        console.log(res.data)
        //emit   
        this.$refs.infoForm.$emit("openEvent", res.data)
      })
      .catch(() => {})
    },
    openSummaryForm (index, row) {
      this.$refs.summaryForm.$emit("openEvent")
      // let para = {page: this.page, url: row.url}
      // apiGetPatientSummaryForm(para)
      // .then((res)=> {
      //   console.log(res.data)
      //   //emit   
      //   this.$refs.summaryForm.$emit("openEvent", res.data)
      // })
      // .catch(() => {})
    },
    openHistoryForm (index, row) {
      let para = {page: this.page, url: row.url}
      apiGetPatientHistoryForm(para)
      .then((res)=> {
        console.log(res.data)
        //emit   
        this.$refs.historyForm.$emit("openEvent", res.data)
      })
      .catch(() => {})
    },
    openExperimentForm (index, row) {
      let para = {page: this.page, url: row.url}
      apiGetPatientExperimentForm(para)
      .then((res)=> {
        console.log(res.data)
        //emit   
        this.$refs.experimentForm.$emit("openEvent", res.data)
      })
      .catch(() => {})
    },
    openBxrayForm (index, row) {
      let para = {page: this.page, url: row.url}
      apiGetPatientBxrayForm(para)
      .then((res)=> {
        console.log(res.data)
        //emit   
        this.$refs.bxrayForm.$emit("openEvent", res.data)
      })
      .catch(() => {})
    },
    openCureForm (index, row) {
      let para = {page: this.page, url: row.url}
      apiGetPatientCureForm(para)
      .then((res)=> {
        console.log(res.data)
        //emit   
        this.$refs.cureForm.$emit("openEvent", res.data)
      })
      .catch(() => {})
    },

  },
  mounted () {
    this.getPatients()
  }
}

</script>

<style scoped>

</style>



