<template>
  <el-dialog title="" :visible.sync="dialogVisible" :close-on-click-modal="false" width="100%">
    <el-form ref="infoForm" :model="infoForm" label-width="130px" label-position="left">
      <el-form-item label="">
        <el-input v-model="infoForm.name"></el-input>
        <!-- <el-input v-model="infoForm.name"><template slot="prepend"></template></el-input> -->
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="infoForm.nation" placeholder="">
          <el-option v-for="item in nationSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="infoForm.career" placeholder="">
          <el-option v-for="item in careerSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-checkbox v-for="item in specialCheckbox" :key="item.content" :label="item.content" v-model="infoForm[item.name]">
        </el-checkbox>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.address"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-checkbox v-for="item in dietCheckbox" :key="item.content" :label="item.content" v-model="infoForm[item.name]">
        </el-checkbox>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.height"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.weight"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.hipline"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.waistline"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.hairy"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="infoForm.acne" active-text="" inactive-text=""></el-switch>
        <div v-if="infoForm.acne">
          <el-form-item label="">
            <el-input v-model="infoForm.acne_part"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-slider v-model="infoForm.acne_score" :max="5" show-stops></el-slider>
          </el-form-item>
            <el-table :data="acneData" style="width: 100%">
              <el-table-column prop="field1" label=""></el-table-column>
              <el-table-column prop="field2" label=""></el-table-column>
              <el-table-column prop="field3" label=""></el-table-column>
            </el-table>
        </div>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="infoForm.glandula" active-text="" inactive-text=""></el-switch>
        <div v-if="infoForm.glandula">
          <el-form-item label="">
            <el-input v-model="infoForm.glandula_part"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio v-model="infoForm.glandula_level" label=""></el-radio>
            <el-radio v-model="infoForm.glandula_level" label=""></el-radio>
            <el-radio v-model="infoForm.glandula_level" label=""></el-radio>
          </el-form-item>
        </div>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="infoForm.male" active-text="" inactive-text=""></el-switch>
        <div v-if="infoForm.male">
          <el-form-item label="">
            <el-input v-model="infoForm.male_part"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio v-model="infoForm.male_level" label=""></el-radio>
            <el-radio v-model="infoForm.male_level" label=""></el-radio>
            <el-radio v-model="infoForm.male_level" label=""></el-radio>
          </el-form-item>
        </div>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="updateInfoForm"></el-button>
        <el-button @click="dialogVisible=false"></el-button>
      </el-form-item>

    </el-form>
  </el-dialog>
</template>
<script>
import { apiUpdatePatientInfoForm } from '@/api/api'
export default {
  name: "InfoForm",
  data() {
    return {
      dialogVisible: false,
      infoForm: {},
      nationSelection: ["","","","","","","","","","","","",
                        "","","","","","","","","","","","",
                        "","","","","","","","","","","",
                        "","","","","","","","","","",
                        "","","","","","","","","","","",""],
      careerSelection: ["","","","","","","","","","","",""],
      specialCheckbox: [{content:"", name:"special_gaowen"},{content:"", name:"special_diwen"},{content:"", name:"special_yeban"},
                        {content:"", name:"special_zao"},{content:"", name:"special_fu"},{content:"", name:"special_hua"},
                        {content:"", name:"special_ju"},{content:"", name:"special_qi"},{content:"", name:"special_kong"},
                        {content:"", name:"special_wu"}],
      dietCheckbox:    [{content:"",name:"yinshi_wuteshu"},{content:"",name:"yinshi_sushi"},{content:"",name:"yinshi_suan"},
                        {content:"",name:"yinshi_xian"},{content:"",name:"yinshi_xinla"},{content:"",name:"yinshi_you"},
                        {content:"",name:"yinshi_shengleng"},{content:"",name:"yinshi_cafei"},{content:"",name:"yinshi_qita"}],
      acneData:[{field1: '0', field2: '', field3: ''},
                {field1: '1', field2: '', field3: '2mm<10'},
                {field1: '2', field2: '', field3: '10-20'},
                {field1: '3', field2: '', field3: '>20<20'},
                {field1: '4', field2: '', field3: '20'},
                {field1: '5', field2: '', field3: '5mm'}],
      }
  },
  methods: {
    updateInfoForm () {
      let para = {
        url: this.infoForm.url,
        infoForm: this.infoForm
      }
      console.log("",para)
      apiUpdatePatientInfoForm(para)
      .then((res)=> {
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
        // this.$message({message: '',type: 'error'})
      )
    }
  },
  created() {
      this.$on("openEvent", (data)=>{
        this.infoForm = data;
        this.dialogVisible = true;
      });
    }
};
</script>



